@import "../../mediaqueries.scss";
@import "../../theme.scss";

.playlist {
  height: 100%;
  display: flex;
  flex-direction: column;

  .playlist-operation {
    display: flex;
    height: 45px;
    background: rgba(255, 255, 255, 1);
    border-radius: 3px;
    border: 1px solid rgba(235, 235, 235, 1);
    padding: 0 15px;

    .checkAll {
      align-self: center;
      display: flex;
      align-items: center;

      .check-frame {
        margin-right: 5px;
        cursor: pointer;
        border-radius: 5px;
        font-size: 20px;
      }
      .icon-xuanzhong1{
        color: $bg-color;
      }

      .checkAll-text,.invert-Selection {
        color: rgba(2, 2, 2, 1);
        line-height: 20px;
        margin-right: 24px;
      }
      .invert-Selection {
        cursor: pointer;
        color: $bg-color;
      }
    }

    .playlist-delete {
      align-self: center;
      padding-left: 24px;
      border-left: 1px solid #e9e9e9;

      span {
        display: block;
        height: 30px;
        padding: 0 10px;
        border-radius: 3px;
        border: 1px solid rgba(191, 191, 191, 1);
        font-weight: 400;
        color: rgba(31, 31, 31, 1);
        line-height: 30px;
        cursor: pointer;

        i {
          margin-right: 10px;
        }
      }
    }
  }

  .playlist-main {
    flex: 1;

    p {
      margin: 0;
    }
  }
}